<template>
  <div>
    <!-- 卡片 -->
    <el-card>
      <h1>欢迎使用「Willen's 电商后台管理系统」</h1>
      <el-alert title="请点击左侧菜单栏以选择功能" type="info" show-icon="">
      </el-alert>
      <p>今天是{{ date }}。祝您工作愉快 :)</p>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      date: '',
      weather: ''
    }
  },
  mounted() {
    this.getDate()
  },
  methods: {
    getDate() {
      var myDate = new Date()
      var weekDay = ['日', '一', '二', '三', '四', '五', '六']
      this.date = ` ${myDate.getFullYear()} 年 ${myDate.getMonth() +
        1} 月 ${myDate.getDate()} 日，星期${weekDay[myDate.getDay()]}`
    }
  }
}
</script>
